<template>
    <div id="treeChart" :style="{width:'800px',height:'600px',margin:'10px auto'}"></div>
</template>
<script>
import treeData from './treeChart.json';
export default {
    data(){
        return{
            isFirst:true,
        }
    },
    methods:{
        drawChart(){
            if(!this.isFirst){
                return;
            }
            this.isFirst = false;
            var treeChart = this.$echarts.init(document.getElementById('treeChart'));
            treeChart.showLoading()
            treeChart.setOption({
                tooltip: {
                    trigger: 'item',
                    triggerOn: 'mousemove'
                },
                series: [
                    {
                        type: 'tree',
                        data: [treeData],
                        top: '1%',
                        left: '7%',
                        bottom: '1%',
                        right: '20%',
                        symbolSize: 7,
                        label: {
                            normal: {
                                position: 'left',
                                verticalAlign: 'middle',
                                align: 'right',
                                fontSize: 9
                            }
                        },
                        leaves: {
                            label: {
                                normal: {
                                    position: 'right',
                                    verticalAlign: 'middle',
                                    align: 'left'
                                }
                            }
                        },
                        expandAndCollapse: true,
                        animationDuration: 550,
                        animationDurationUpdate: 750
                    }
                ]
            });
            setTimeout(function(){
                treeChart.hideLoading()
            },1000)
        }
    }
}
</script>




